import { Component, OnInit, Input } from '@angular/core';
import { ApiService } from 'src/app/services/api/api.service';
import { HelperService } from 'src/app/services/helper/helper.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FilterPipe } from 'ngx-filter-pipe';
import { bind } from '@angular/core/src/render3';


@Component({
  selector: 'app-departments',
  templateUrl: './departments.component.html',
  styleUrls: ['./departments.component.css']
})
export class DepartmentsComponent implements OnInit {

  constructor(private api:ApiService, private helper:HelperService,
    private formBuilder: FormBuilder,private filter: FilterPipe) {

    }

  departments:any;
  role;
  departmentForm: FormGroup;
  submitted = false;
  page:number = 1;
  pageSize:number =4;

  searchTerm:any = {title:''};
  order:string ='title';
  reverse=false;



  sortBy(value){
    this.order = value;
  }

  reverseSort(value){
    this.order = value;
    this.reverse = !this.reverse;
  }

  get departmentKeys(){
    if(this.departments.length>0){
      let x= Object.keys(this.departments[0])
      x.splice( x.indexOf('code'), 1 );
      x.splice( x.indexOf('index'), 1 );

      return x;
    }else{
      return []
    }
  }



  ngOnInit() {
  this.getDepartments();
  // this.departments=[
  //   {title:'Atromoa', email:'atroma@gmail.com', phone:'+971555423232'},
  //   {title:'adad', email:'atroma@gmail.com', phone:'+971555423232'},
  //   {title:'three', email:'atroma@gmail.com', phone:'+971555423232'},
  //   {title:'Atromoa', email:'atroma@gmail.com', phone:'+971555423232'},
  //   {title:'four', email:'atroma@gmail.com', phone:'+971555423232'},
  //   {title:'Atromoa', email:'atroma@gmail.com', phone:'+971555423232'},
  //   {title:'five', email:'atroma@gmail.com', phone:'+971555423232'},
  //   {title:'Atromoa', email:'atroma@gmail.com', phone:'+971555423232'},
  //   {title:'six', email:'atroma@gmail.com', phone:'+971555423232'},
  //   {title:'Atromoa', email:'atroma@gmail.com', phone:'+971555423232'},
  //   {title:'seven', email:'atroma@gmail.com', phone:'+971555423232'},
  //   {title:'Atromoa', email:'atroma@gmail.com', phone:'+971555423232'},
  // ]
  this.role = localStorage.getItem('role');
  this.departmentForm = this.formBuilder.group({
  //  _id:[this.selectedDepartment._id],
    code:[this.selectedDepartment.code],
    title: [this.selectedDepartment.title, Validators.required],
    phone: [this.selectedDepartment.phone],
    email: [this.selectedDepartment.email, ],
    status: [this.selectedDepartment.status, ],
    // password: ['', [Validators.required, Validators.minLength(6)]]
});
  }



  view ='table' //graph
  departmentData

  viewDetails(content, data){
    console.log('data',data);
    // th
    if(data.status == true){
      this.helper.open(content,  { size: 'lg' });
    this.api.getDepartmentReports(data.code).subscribe(resp=>{
      console.log('fina-response', resp);
      this.departmentData = resp;
    })
  }else{
    this.helper.toast('danger', 'Error! Department must be approve','Inactive Department' )
  }

  }




  onTextChange(e){
    let val = e.target.value;
    //make the array full
    this.pageSize = this.departments.length;
    console.log('event', e.target.value);
    this.searchTerm.title = e.target.value;
 //   this.departments = this.filter.transform(this.departments, { title: this.searchTerm.title });

  }


  getDepartments(){
   this.api.getAllDepartments()
    .subscribe(res=>{
      let data = res;
      let index =0;
     data = data.map((key,val)=>{
       index++;
       return {...key, index}
     }  );
     this.departments = data;
     console.log('departments', this.departments);


    },err=>this.handleError(err))

  }

  selectedDepartment={
    code:'',
    title:'',
    email:'',
    phone:'',
    status:true
  }



  handleError(err){
    console.log('Error: ', err.message);

  }

  add(c){
    this.helper.open(c)
  }

   // convenience getter for easy access to form fields
   get f() { return this.departmentForm.controls; }


  onSubmit() {
    this.submitted = true;
    console.log('mai chala')

    // stop here if form is invalid
    if (this.departmentForm.invalid) {
      console.log('invalid department form!')
        return;
    }
    console.log(this.departmentForm.value);
    let data = this.departmentForm.value;
    delete data._id; delete data.code;
    console.log(data);
    this.api.createDepartment(data)
    .subscribe(res=>{
      console.log('res',res);
      this.helper.toast('success', 'Department Created', 'Your department is active');
      this.helper.closeModal();
      this.getDepartments();
    });
  }


  onUpdate() {
    this.submitted = true;
    console.log('mai chala')
    // stop here if form is invalid
    if (this.departmentForm.invalid) {
      console.log('invalid department form!')
        return;
    }
    let data = Object.assign({}, this.departmentForm.value)
    let code = data.code;
    delete data.code;
    this.api.updateDepartment(code,data.value)
    .subscribe(res=>{
      console.log('res',res);
      this.helper.toast('success', 'Department Updated', 'Your department is active');
      this.helper.closeModal();
      this.getDepartments();
    });
  }


  closeModal(){
    this.departmentData ={};
    this.helper.closeModal();
  }


  onDelete() {
    this.submitted = true;
    this.api.deleteDepartment(this.selectedDepartment.code)
    .subscribe(res=>{
      console.log('res',res);
      this.helper.toast('success', 'Department Deleted', 'Department successfully deleted.');
      this.helper.closeModal();
      this.getDepartments();
    });
  }

  edit(c, data?){
    console.log('selected department', data);
    this.helper.open(c)
    this.departmentForm.setValue(data);

  }


  delete(c, data){
    console.log('selected department', data);
    this.selectedDepartment.code = data.code;
    this.helper.open(c);

    // this.api.deleteDepartment();

  }



}
