import { Injectable } from '@angular/core';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';

@Injectable({
  providedIn: 'root'
})
export class HelperService {
  closeResult: string;

  constructor(private modalService: NgbModal,private toastr: ToastrService) { }




  /*
  * Open a Modal
   */
  open(content, settings?) {
    if(!settings)
    settings = {};
    this.modalService.open(content, settings);
  }

  closeModal(){
    this.modalService.dismissAll();
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }



  toast(type, title, msg) {

    switch(type){
      case 'success':  this.toastr.success(title, msg);break;
      case 'danger':  this.toastr.error(title, msg);break;
      case 'warning':  this.toastr.warning(title, msg);break;
      case 'info':  this.toastr.info(title, msg);break;

    }
  }
}
