import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService } from 'src/app/services/api/api.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  submitted = false;
  errMessage ='';

  constructor(private formBuilder: FormBuilder,private router:Router, private api: ApiService) { }

  ngOnInit() {
      this.loginForm = this.formBuilder.group({
          email: ['atrixdigital@gmail.com', [Validators.required, Validators.email]],
          password: ['atrix123', [Validators.required]]
      });
  }

  // convenience getter for easy access to form fields
  get f() { return this.loginForm.controls; }

  onSubmit() {
      this.submitted = true;
      console.log('daba hai')

        console.log(this.loginForm.value);
        console.log('valid form');
        this.api.login(this.loginForm.value.email, this.loginForm.value.password)
        .subscribe(res=>{
          console.log('res', res);
        //  this.router.navigateByUrl('/dashboard')
          try{
            localStorage.setItem('token', res.token);
            localStorage.setItem('role', res.role);
            localStorage.setItem('uid', res._id);
            this.router.navigate(['dashboard']).then(()=>{
              console.log('routed');
              
            })
          }catch(error){
            console.log('exception', error);
          }



        }, err=>{
          console.log('ERROR ', err.error.message);
          this.errMessage = err.error.message;
        })


  }
}
