import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {  HttpClientModule } from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { DashboardComponent } from './pages/dashboard/dashboard.component';
import { LoginComponent } from './pages/login/login.component';
import { SignupComponent } from './pages/signup/signup.component';
import { ReportsComponent } from './pages/reports/reports.component';
import { StaffComponent } from './pages/staff/staff.component';
import { HomeComponent } from './pages/home/home.component';
import { ManagersComponent } from './pages/managers/managers.component';
import { HeaderComponent } from './shared/header/header.component';
import { FooterComponent } from './shared/footer/footer.component';
import { SidebarComponent } from './shared/sidebar/sidebar.component';
import { PagenotfoundComponent } from './shared/pagenotfound/pagenotfound.component';
import { DepartmentsComponent } from './pages/departments/departments.component';
import { AuthGuard } from './guards/auth.guard';
import { AreasComponent } from './pages/areas/areas.component';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import {NgbPaginationModule, NgbAlertModule, NgbModule, NgbModalModule} from '@ng-bootstrap/ng-bootstrap';
import { ToastrModule } from 'ngx-toastr';
import { FilterPipeModule } from 'ngx-filter-pipe';
import { OrderModule } from 'ngx-order-pipe';
import { NgxSelectModule, INgxSelectOptions } from 'ngx-select-ex';
import { PlotlyModule } from 'angular-plotly.js';
import { ApprovedComponent } from './pages/approved/approved.component';
import { DlDateTimeDateModule, DlDateTimePickerModule } from 'angular-bootstrap-datetimepicker';
import { DepartmentViewsComponent } from './pages/department-views/department-views.component';


const CustomSelectOptions: INgxSelectOptions = { // Check the interface for more options
  optionValueField: 'code',
  optionTextField: 'title',
  keepSelectedItems:false,
  autoSelectSingleOption:true,
  allowClear:true
};

@NgModule({
  declarations: [
    AppComponent,
    DashboardComponent,
    LoginComponent,
    SignupComponent,
    ReportsComponent,
    StaffComponent,
    HomeComponent,
    ManagersComponent,
    HeaderComponent,
    FooterComponent,
    SidebarComponent,
    PagenotfoundComponent,
    DepartmentsComponent,
    AreasComponent,
    ApprovedComponent,
    DepartmentViewsComponent,
  ],
  imports: [
    BrowserModule,
    FilterPipeModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    BrowserAnimationsModule,
    NgxSelectModule.forRoot(CustomSelectOptions),
    ToastrModule.forRoot(),
    PlotlyModule,
    OrderModule,
    NgbModule,
    NgbPaginationModule, NgbAlertModule,NgbModalModule,
      DlDateTimeDateModule,  // <--- Determines the data type of the model
    DlDateTimePickerModule,

    AppRoutingModule,
  ],
  providers: [AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }
