  import { Component, OnInit, Input } from '@angular/core';
  import { ApiService } from 'src/app/services/api/api.service';
  import { HelperService } from 'src/app/services/helper/helper.service';
  import { FormBuilder, FormGroup, Validators } from '@angular/forms';
  import { FilterPipe } from 'ngx-filter-pipe';
import { NgbCalendar } from '@ng-bootstrap/ng-bootstrap';

  @Component({
    selector: 'app-reports',
    templateUrl: './reports.component.html',
    styleUrls: ['./reports.component.css']
  })
  export class ReportsComponent implements OnInit {
  viewType = 'table'; //table || graph || details

    constructor(private api:ApiService, private helper:HelperService,private calendar: NgbCalendar,
      private formBuilder: FormBuilder,private filter: FilterPipe) {

      }
      public graph
    departments;
    areas;
    managers;
    reports:any;
    role;
    reportForm: FormGroup;
    submitted = false;
    page:number = 1;
    pageSize:number =4;

    searchTerm:any = {department:''};
    order:string ='';
    reverse=false;


    getManagers(){
      this.api.getManagers().subscribe(resp=>{
        console.log('all managers estemeed',resp);
        this.managerx = resp;
      })
    }


    selectedArea={
      area: "",
      breached: null,
      created_at: "",
      department: null,
      endTime: "",
      index: null,
      majors: null,
      manager:{},
      paediatrics: null,
      resus: null,
      startTime: "",
      submitted: false,
      submittedBy: null,
      totalPatients: null,
      triage: null,
      updated_at: "",
      utc: null,
      waitingRoom: null,
      code:'',
      _id:''
    }

    sortBy(value){
      this.order = value;
    }

    reverseSort(value){
      this.order = value;
      this.reverse = !this.reverse;
    }

    get reportKeys(){
      if(this.reports.length>0){
        let x= Object.keys(this.reports[0])
        x.splice( x.indexOf('code'), 1 );
        x.splice( x.indexOf('index'), 1 );

        return x;
      }else{
        return []
      }
    }


    y=[];
    selectedReport


    backFromGraph(){
      this.viewType ='table';
      this.selectedReport ={};
    }


    viewDetails(val){
      this.viewType ='details';
      this.selectedReport = val;



    }

    viewBarGraph(index, val){
      this.viewType ='graph';
      this.selectedReport = val;
    this.graph={
      data:[
        { x: ['totalPatients','waitingRoom','Triage', 'UTC','Majors','Paediatrics','resus','Breached'],
          y: this.y[index],
          type:'bar',
            marker:{color:['#F44336','#00BCD4','#4CAF50','#3F51B5','#FF5722','#CDDC39','#5D4037','#EC407A']}
          }
        ]
    }
    }

    date(date){
      return new Date(date).toLocaleString();
    }

    viewPieGraph(index, val){
      this.viewType ='graph';
      this.selectedReport = val;
      [{
        values: [19, 26, 55],
        labels: ['Residential', 'Non-Residential', 'Utility'],
        type: 'pie'
      }];
    this.graph={
      data:[
        { labels: ['totalPatients','waitingRoom','Triage', 'UTC','Majors','Paediatrics','resus','Breached'],
          values: this.y[index],
          type:'pie',
            //marker:{color:['#F44336','#00BCD4','#4CAF50','#3F51B5','#FF5722','#CDDC39','#5D4037','#EC407A']}
          }
        ]
    }
    }

    managerx=[]
    ngOnInit() {
      this.api.getAllReports()
      .subscribe(res=>{
        let data = res;
        let index =0;
      data = data.map((key,val)=>{
        index++;
        return {...key, index}
      });
      console.log(data);
      //  let y= ['totalPatients','waitingRoom','Triage', 'UTC','Majors','Paediatrics','resus','Breached'];
      this.y = data.map((key,val)=> {
        return [key.totalPatients, key.waitingRoom, key.triage, key.utc, key.majors, key.paediatrics,key.resus, key.breached]
      });
      this.graph={
        data:[
          { x: ['totalPatients','waitingRoom','Triage', 'UTC','Majors','Paediatrics','resus','Breached'],
            y:this.y[0],
            type:'bar',
            marker:{color:['#F44336','#00BCD4','#4CAF50','#3F51B5','#FF5722','#CDDC39','#5D4037','#EC407A']}
            }
        ]
      }



      this.reports = data;
      // console.log('reports', this.reports);


      },err=>this.handleError(err))

      this.api.getAllDepartments().subscribe(response=>{
        // console.log('dp', response);
        this.departments = response;
        console.log('deparmtnets chale', this.departments);
        this.api.getManagers().subscribe(m=>{
          console.log('all managers utimate',m);
          let arr = m;
         this.managers= arr.map(mana=>{
            return {
              code:mana._id,
              title:mana.username
            }
          })
        })
      })
    this.role = localStorage.getItem('role');
    this.reportForm = this.formBuilder.group({
    //  _id:[this.selectedArea._id],
    code:[this.selectedArea.code],
      department: [this.selectedArea.department],
      area: [this.selectedArea.area],
      breached: [this.selectedArea.breached],
      created_at: [this.selectedArea.created_at],
      endTime: [this.selectedArea.endTime],
      index: [this.selectedArea.index],
      majors: [this.selectedArea.majors],
      manager:[this.selectedArea.manager],
      paediatrics: [this.selectedArea.paediatrics],
      resus: [this.selectedArea.resus],
      startTime: [this.selectedArea.startTime],
      submitted: [this.selectedArea.submitted],
      submittedBy: [this.selectedArea.submittedBy],
      totalPatients: [this.selectedArea.totalPatients],
      triage: [this.selectedArea.triage],
      updated_at: [this.selectedArea.updated_at],
      utc: [this.selectedArea.utc],
      waitingRoom: [this.selectedArea.waitingRoom],
      // password: ['', [Validators.required, Validators.minLength(6)]]
  });

    }



    onTextChange(e){
      console.log(e);
      let val = e.target.value;
      //make the array full
      this.pageSize = this.reports.length;
      console.log('event', e.target.value);
      this.searchTerm.department = e.target.value;
  //   this.reports = this.filter.transform(this.reports, { title: this.searchTerm.title });

    }

    calender = false;


    showCalender(key){
      this.selectedTimeType = key;
      this.calender =  true;
    }


    selectedTimeType= 'startTime';
    setTimeValue( e){
      console.log('d',e.value);
      let value = e.value;
      this.reportForm.controls[this.selectedTimeType].setValue(value);
       this.calender =false;
    }


    getReports(){
      this.api.getAllReports()
      .subscribe(res=>{
        let data = res;
        let index =0;
      data = data.map((key,val)=>{
        index++;
        return {...key, index}
      });
      });
    }




    handleError(err){
      console.log('Error: ', err.message);

    }

    add(c){
      this.helper.open(c)
    }

    // convenience getter for easy access to form fields
    get f() { return this.reportForm.controls; }


    onSubmit() {
      this.submitted = true;
      console.log('mai chala')

      // stop here if form is invalid
      if (this.reportForm.invalid) {
        console.log('invalid report form!')
          return;
      }
      console.log(this.reportForm.value);
      let data = this.reportForm.value;
      delete data._id; delete data.code;
      console.log(data);
      this.api.createReport(data)
      .subscribe(res=>{
        console.log('res',res);
        this.helper.toast('success', 'Report Created', 'Your Report created.');
        this.helper.closeModal();
        this.getReports();
      });
    }


    onUpdate() {
      this.submitted = true;
      console.log('mai chala')
      // stop here if form is invalid
      if (this.reportForm.invalid) {
        console.log('invalid department form!')
          return;
      }
      let data = Object.assign({}, this.reportForm.value)
      let code = data.code;
      delete data.code;
      this.api.updateDepartment(code,data.value)
      .subscribe(res=>{
        console.log('res',res);
        this.helper.toast('success', 'Department Updated', 'Your department is active');
        this.helper.closeModal();
        this.getReports();
      });
    }


    onDelete() {
      this.submitted = true;
      this.api.deleteDepartment(this.selectedArea.code)
      .subscribe(res=>{
        console.log('res',res);
        this.helper.toast('success', 'Department Deleted', 'Department successfully deleted.');
        this.helper.closeModal();
        this.getReports();
      });
    }

    edit(c, data?){
      console.log('selected department', data);
      this.helper.open(c)
      this.reportForm.setValue(data);

    }


    delete(c, data){
      console.log('selected department', data);
      this.selectedArea.code = data.code;
      this.helper.open(c);

      // this.api.deleteDepartment();

    }



    getDepartments(){
      this.api.getAllDepartments(res=>{
      this.departments = res;
      console.log('departments', this.departments);
      })
    }
    getAreas(){
      this.api.getAllAreas(res=>{
      this.areas = res;
      console.log('areas', this.areas);

      })
    }


    getx(e){
      this.areas =[];
      console.log('e',e);
      this.api.getDepartmentAreas(e).subscribe(res=>{
        if(res){
          console.log('areas aagaye', res);
          this.areas = res;
        }
      });
    }


  }
