import { Component, OnInit, Input } from '@angular/core';
import { ApiService } from 'src/app/services/api/api.service';
import { HelperService } from 'src/app/services/helper/helper.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FilterPipe } from 'ngx-filter-pipe';
import { bind } from '@angular/core/src/render3';
import { NgxSelectModule, INgxSelectOptions } from 'ngx-select-ex';

@Component({
  selector: 'app-approved',
  templateUrl: './approved.component.html',
  styleUrls: ['./approved.component.css']
})
export class ApprovedComponent implements OnInit {

  constructor(private api:ApiService, private helper:HelperService,
    private formBuilder: FormBuilder,private filter: FilterPipe) {

    }

  users:any;
  role;
  userForm: FormGroup;

  submitted = false;
  page:number = 1;
  pageSize:number =15;
  departments;

  searchTerm:any = {username:''};
  order:string ='username';
  reverse=false;



  selectedUser={
    _id:'',
    role:'',
    username:'',
    password:'',
    department:'',
    approved:false
  }

  open(content,data){
    this.selectedUser = data;
    this.helper.open(content)
  }


  sortBy(value){
    this.order = value;
  }

  reverseSort(value){
    this.order = value;
    this.reverse = !this.reverse;
  }

  get departmentKeys(){
    if(this.users.length>0){
      let x= Object.keys(this.users)
      //x.splice( x.indexOf('code'), 1 );
      //x.splice( x.indexOf('index'), 1 );
      return x;
    }else{
      return []
    }
  }



  ngOnInit() {

  this.role = localStorage.getItem('role');
this.getUnapprovedUsers();
this.api.getAllDepartments().subscribe(response=>{
  this.departments = response;
})

this.userForm = this.formBuilder.group({
  //  _id:[this.selectedUser._id],
    username:[this.selectedUser.username, Validators.required],
    password: [this.selectedUser.password, Validators.required],
    role: [this.selectedUser.role, Validators.required],
    department: [this.selectedUser.department, ],
    approved: [this.selectedUser.approved, ],
    // password: ['', [Validators.required, Validators.minLength(6)]]
});

  }


  onTextChange(e){
    let val = e.target.value;
    //make the array full
  this.pageSize = this.users.length;
    console.log('event', e.target.value);
    this.searchTerm.username = e.target.value;
 //   this.users = this.filter.transform(this.users, { title: this.searchTerm.title });

  }




  getAreas(){
  this.users = this.api.getAllAreas()
    .subscribe(res=>{
      let data = res;
      let index =0;
     data = data.map((key,val)=>{
       index++;
       return {...key, index}
     }  );
     this.users = data;
     console.log('users', this.users);


    },err=>this.handleError(err))

  }




  handleError(err){
    console.log('Error: ', err.message);

  }

  add(c){
    this.helper.open(c)
  }

   // convenience getter for easy access to form fields
   get f() { return this.userForm.controls; }


  onSubmit() {
    this.submitted = true;
    console.log('mai chala')

    // stop here if form is invalid
    if (this.userForm.invalid) {
      console.log('invalid user form!')
        return;
    }
    console.log(this.userForm.value);
    let data = this.userForm.value;
    data.approved = Boolean(data.approved);
    delete data._id; delete data.code;
    console.log(data);
    this.api.createUser(data)
    .subscribe(res=>{
      console.log('res',res);
      this.helper.toast('success', 'User Created', 'Your user is active');
      this.helper.closeModal();
      this.getUnapprovedUsers();
    });
  }


  onUpdate() {
    this.submitted = true;
    console.log('mai chala')
    // stop here if form is invalid
    if (this.userForm.invalid) {
      console.log('invalid User form!')
        return;
    }
    let data = Object.assign({}, this.userForm.value)
    let code = data.code;
    delete data.code;
    this.api.updateDepartment(code,data.value)
    .subscribe(res=>{
      console.log('res',res);
      this.helper.toast('success', 'User Updated', 'Your User is active');
      this.helper.closeModal();
      this.getUnapprovedUsers();
    });
  }


  onDelete() {
    this.submitted = true;
    this.api.deleteUser(this.selectedUser._id)
    .subscribe(res=>{
      console.log('res',res);
      this.helper.toast('success', 'User Deleted', 'User successfully deleted.');
      this.helper.closeModal();
      this.getUnapprovedUsers();
    });
  }

  edit(c, data?){
    console.log('selected User', data);
    this.helper.open(c)
    this.userForm.setValue(data);

  }


  delete(c, data){
    console.log('selected department', data);
   // this.selectedUser.code = data.code;
    this.helper.open(c);

    // this.api.deleteDepartment();

  }



  // getDepartments(){
  //   this.api.getAllDepartments(res=>{
  //   this.departments = res;
  //   console.log('departments', this.departments);
  //   })
  // }


  getUnapprovedUsers(){
this.api.getUnapprovedUsers().subscribe(res=>{
  this.users = res;
  console.log(res);
})
  }






  // Approve user

  approveUser(id){
    this.api.updateUser(id, {approved:true}).subscribe(resp=>{
      this.getUnapprovedUsers();
      this.helper.closeModal();
      this.helper.toast('success', 'User Updated', 'User Approved');

    })
  }

}
